import React, { useState, useEffect, useRef } from 'react';

function parseSchedule(rawText, baseStartTime = null) {
    const now = baseStartTime ? new Date(baseStartTime) : new Date();
    let currentTime = new Date(now);
    return rawText
        .split('\n')
        .filter(line => line.trim())
        .map((line, index) => {
            // Regex to find a number + time unit (e.g. 30 min, 1 hr, 45 seconds)
            const timeRegex = /(\d+)\s*(sec|s|seconds|min|mins|minutes|hr|hrs|hour|hours)\b/i;
            const match = line.match(timeRegex);

            let title = '', duration = '', notes = '';
            if (match) {
                const before = line.slice(0, match.index).trim();
                const after = line.slice(match.index + match[0].length).trim();
                title = before.replace(/^|^-\s*/, '').trim();
                duration = match[0].trim();
                notes = after;
            } else {
                // fallback: treat as title only
                title = line.trim();
                duration = '';
                notes = '';
            }

            // Convert time to seconds
            let timeInSeconds = 1800; // default 30 min
            if (duration) {
                const [numStr, unit] = duration.split(/\s+/);
                const num = parseInt(numStr, 10);
                if (/^s(ec|econds)?$/i.test(unit)) timeInSeconds = num;
                else if (/^min(ute)?s?$/i.test(unit)) timeInSeconds = num * 60;
                else if (/^hr?s?|hour?s?$/i.test(unit)) timeInSeconds = num * 60 * 60;
            }

            const startTime = new Date(currentTime);
            const endTime = new Date(currentTime.getTime() + timeInSeconds * 1000);
            currentTime = new Date(endTime);

            return {
                id: index,
                title,
                notes,
                duration: timeInSeconds,
                remaining: timeInSeconds,
                isActive: false,
                isCompleted: false,
                startTime: startTime,
                endTime: endTime,
            };
        });
}

const formatTime = (seconds) => {
    const m = Math.floor(seconds / 60).toString().padStart(2, '0');
    const s = (seconds % 60).toString().padStart(2, '0');
    if (s === '00') return `${m}`;
    return `${m}:${s}`;
};

const formatClock = (date) => {
    if (!date) return '';
    const d = new Date(date);
    let time = d.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    // Remove leading zero from hour if present
    if (time[0] === '0') time = time.slice(1);
    
    return time.replace(' PM', 'pm').replace(' AM', 'am');
};

const LOCAL_STORAGE_KEY = 'schedulerTasks';
const LOCAL_STORAGE_META_KEY = 'schedulerMeta';

const Scheduler = () => {
    const [rawInput, setRawInput] = useState('');
    const [tasks, setTasks] = useState([]);
    const [scheduleStart, setScheduleStart] = useState(null);
    const intervalRef = useRef(null);
    const [activeTaskId, setActiveTaskId] = useState(null);

    // Load from localStorage on mount
    useEffect(() => {
        const savedTasks = localStorage.getItem(LOCAL_STORAGE_KEY);
        const savedMeta = localStorage.getItem(LOCAL_STORAGE_META_KEY);
        if (savedTasks && savedMeta) {
            try {
                const parsedTasks = JSON.parse(savedTasks);
                const meta = JSON.parse(savedMeta);
                setRawInput(meta.rawInput || '');
                setScheduleStart(meta.scheduleStart ? new Date(meta.scheduleStart) : null);

                // Calculate time passed since last save
                const now = Date.now();
                const lastSaved = meta.lastSaved ? new Date(meta.lastSaved).getTime() : now;
                const elapsed = Math.floor((now - lastSaved) / 1000);

                let foundActive = false;
                const updatedTasks = parsedTasks.map(task => {
                    if (task.isActive && !task.isCompleted) {
                        foundActive = true;
                        let newRemaining = task.remaining - elapsed;
                        if (newRemaining <= 0) {
                            // Mark as completed and set remaining to 0
                            return { ...task, isActive: false, isCompleted: true, remaining: 0 };
                        }
                        return { ...task, remaining: newRemaining > 0 ? newRemaining : 0 };
                    }
                    return task;
                });

                setTasks(updatedTasks);

                // If a task was active, start its timer
                if (foundActive) {
                    const activeTask = updatedTasks.find(t => t.isActive && !t.isCompleted);
                    if (activeTask) {
                        setActiveTaskId(activeTask.id);
                        startTask(activeTask.id, updatedTasks); // pass updatedTasks to avoid race
                    }
                }
            } catch (e) {
                // If error, clear storage
                localStorage.removeItem(LOCAL_STORAGE_KEY);
                localStorage.removeItem(LOCAL_STORAGE_META_KEY);
            }
        }
        // eslint-disable-next-line
    }, []);

    // Save to localStorage on every tasks/rawInput/scheduleStart change
    useEffect(() => {
        if (tasks.length > 0) {
            localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(tasks));
            localStorage.setItem(LOCAL_STORAGE_META_KEY, JSON.stringify({
                rawInput,
                scheduleStart,
                lastSaved: new Date().toISOString()
            }));
        }
    }, [tasks, rawInput, scheduleStart]);
    useEffect(() => {
        if (
            tasks.length > 0 &&
            tasks.every(t => t.isCompleted || t.skipped) &&
            tasks.some(t => t.skipped && !t.isCompleted)
        ) {
            // Find the first skipped and not completed task
            const firstSkipped = tasks.find(t => t.skipped && !t.isCompleted);
            if (firstSkipped) {
                setTasks(prev =>
                    prev.map(task =>
                        task.id === firstSkipped.id
                            ? { ...task, isActive: true, paused: false, skipped: false }
                            : { ...task, isActive: false }
                    )
                );
                setActiveTaskId(firstSkipped.id);
                startTask(firstSkipped.id);
            }
        }
        // eslint-disable-next-line
    }, [tasks]);

    // Helper to recalculate start/end times for all tasks
    const recalculateTimes = (tasksArr, fromIndex = 0, baseTime = null) => {
        let currentTime = baseTime ? new Date(baseTime) : (tasksArr[fromIndex]?.startTime ? new Date(tasksArr[fromIndex].startTime) : new Date());
        return tasksArr.map((task, idx) => {
            if (idx < fromIndex) return task;
            const startTime = new Date(currentTime);
            const endTime = new Date(startTime.getTime() + task.remaining * 1000);
            currentTime = new Date(endTime);
            return { ...task, startTime, endTime };
        });
    };

    // Modified startTask to optionally accept a tasks array (for restoring on reload)
    const startTask = (id, tasksOverride = null) => {
        if (intervalRef.current) clearInterval(intervalRef.current);

        setTasks(prev => {
            const prevTasks = tasksOverride || prev;
            const startIdx = prevTasks.findIndex(task => task.id === id);
            const now = new Date();
            const updated = recalculateTimes(
                prevTasks.map((task, idx) =>
                    idx === startIdx
                        ? { ...task, isActive: true, isCompleted: false, startTime: now, endTime: new Date(now.getTime() + task.remaining * 1000) }
                        : idx > startIdx
                            ? { ...task, isActive: false, isCompleted: false }
                            : { ...task, isActive: false }
                ),
                startIdx,
                now
            );
            return updated;
        });
        setActiveTaskId(id);

        intervalRef.current = setInterval(() => {
            setTasks(prev => {
                const startIdx = prev.findIndex(task => task.id === id);
                let updated = prev.map(task => ({ ...task }));
                if (updated[startIdx].remaining > 0) {
                    updated[startIdx].remaining -= 1;
                    updated[startIdx].endTime = new Date(updated[startIdx].endTime.getTime() - 1000);
                }
                // If finished, mark as completed and start next
                if (updated[startIdx].remaining <= 0) {
                    clearInterval(intervalRef.current);
                    updated[startIdx].isCompleted = true;
                    updated[startIdx].isActive = false;
                    updated[startIdx].remaining = 0;
                    // Recalculate times for subsequent tasks
                    updated = recalculateTimes(updated, startIdx + 1, updated[startIdx].endTime);
                    // Start next task if exists
                    const next = updated.find((t, idx) => idx > startIdx && !t.isCompleted);
                    if (next) startTask(next.id);
                    else setActiveTaskId(null);
                }
                return updated;
            });
        }, 1000);
    };

    const pauseTask = () => {
        if (intervalRef.current) clearInterval(intervalRef.current);
        console.log(`pauseTask => activeTaskId: ${activeTaskId} tasks: ${JSON.stringify(tasks, null, 2)}`);
        setTasks(prev => prev.map(t => ((t.id === activeTaskId) ? { ...t, isActive: false, paused: true } : { ...t, isActive: false, paused: false })));
        setActiveTaskId(null);
    };

    const resetTask = (id) => {
        setTasks(prev => {
            const resetIdx = prev.findIndex(task => task.id === id);
            let updated = prev.map((task, idx) =>
                idx >= resetIdx
                    ? {
                        ...task,
                        remaining: task.duration,
                        isActive: false,
                        isCompleted: false,
                    }
                    : { ...task, isActive: false }
            );
            updated = recalculateTimes(updated, resetIdx, resetIdx === 0 ? scheduleStart : updated[resetIdx - 1].endTime);
            return updated;
        });
        if (intervalRef.current) clearInterval(intervalRef.current);
        setActiveTaskId(null);
    };

    const handleParse = () => {
        const now = new Date();
        setScheduleStart(now);
        const parsed = parseSchedule(rawInput, now);
        setTasks(parsed);
        if (intervalRef.current) clearInterval(intervalRef.current);
        setActiveTaskId(null);
    };
    const skipTask = (id) => {
        setTasks(prev => {
            const idx = prev.findIndex(task => task.id === id);
            // Mark the current task as skipped/paused
            let updated = prev.map((task, i) =>
                i === idx
                    ? { ...task, isActive: false, paused: true, skipped: true }
                    : { ...task, isActive: false }
            );
            // Find the next uncompleted, unpaused, unskipped task
            const next = updated.find((t, i) => i > idx && !t.isCompleted && !t.paused && !t.skipped);
            if (next) {
                // Activate the next task
                updated = updated.map((task, i) =>
                    task.id === next.id
                        ? { ...task, isActive: true, paused: false }
                        : task
                );
            } else {
                // If no next, clear activeTaskId
                setActiveTaskId(null);
            }
            return updated;
        });
        if (intervalRef.current) clearInterval(intervalRef.current);
        // Find the next task to start
        setTimeout(() => {
            setTasks(prev => {
                const next = prev.find((t, i) => !t.isCompleted && !t.paused && !t.skipped);
                if (next) {
                    setActiveTaskId(next.id);
                    startTask(next.id);
                }
                return prev;
            });
        }, 0);
    };

    return (
        <div className='containerBox'>
            <div className='containerBox contentLeft color-yellow'> Timed Schedule Tracker</div>
            <textarea
                className='containerBox'
                rows={10}
                style={{ width: '100%', padding: 10, fontSize: 16 }}
                placeholder="Paste your schedule here..."
                value={rawInput}
                onChange={(e) => setRawInput(e.target.value)}
            />
            <div className='containerBox button bg-green' onClick={handleParse}>
                Parse Schedule
            </div>

            {tasks.map((task) => (
                <div
                    key={task.id}
                    className={`containerBox ${task.isCompleted ? 'bg-dkGreen' : task.isActive ? 'bg-silver' : task.paused ? 'bg-dkYellow' : 'bg-lite'}`}
                >
                    <div className='containerBox contentLeft color-yellow'>{task.title}</div>
                    <div className='containerBox contentLeft'>{task.notes}</div>
                    <div className='containerBox contentLeft'>Time: {formatTime(task.remaining)}</div>
                    <div className='containerBox contentLeft'>
                        {formatClock(task.startTime)} - {formatClock(task.endTime)}
                    </div>
                    <div className='containerBox'>
                        {!task.isCompleted && (
                            <div className='containerBox flexContainer'>
                                {
                                    (task.isActive)
                                    ? null
                                    : <div className='containerBox flex2Column button bg-lite' onClick={() => startTask(task.id)} disabled={task.isActive}>
                                             Start
                                        </div>
                                }
                                {
                                    (!task.isActive)
                                    ? null
                                    : <div className='containerBox flex2Column button bg-lite' onClick={pauseTask}>
                                             Pause
                                        </div>
                                }
                                {
                                    (!task.isActive)
                                        ? null
                                        : <div className='containerBox flex2Column button bg-lite' onClick={() => skipTask(task.id)}>
                                             Skip
                                        </div>
                                }
                                <div className='containerBox flex2Column button bg-lite' onClick={() => resetTask(task.id)}> Reset</div>
                            </div>
                        )}
                        {task.isCompleted && <span style={{ color: 'green' }}> Completed</span>}
                    </div>
                </div>
            ))}
        </div>
    );
};

export default Scheduler;